.navbar {
    padding: 6px 35px;
    border-bottom: 1px solid rgb(199, 199, 199);
}

.navbar a {
    /* color: black !important; */
}

.navbar .navbar-brand {
    font-family: 'Cinzel Decorative', cursive;
    font-size: 1.8rem;
    font-weight: bold;
}

.navbar .items {
    /* font-family: Helvetica, sans-serif; */
    /* font-weight: normal; */
    /* font-size: 1rem; */
    min-width: 600px;
    justify-content: space-around;
}

.navbar .items a:hover {
    /* color: rgb(17, 109, 255) !important; */
}

.navbar .social-links {
    font-size: 2rem;
    flex-direction: row;
}

.navbar .social-links a {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0;
    padding-bottom: 0;
}

.navbar .social-links a:hover {
    /* color: rgb(17, 109, 255) !important; */
}

.navbar .social-links a:first-of-type {
    padding-left: 0;
}

.navbar .social-links a:last-of-type {
    padding-right: 0;
}

.jumbotron h1 {
    font-size: 80px;
}

.jumbotron p {
    font-size: 20px;
}

@media (max-width: 960px) {
    .jumbotron h1 {
        font-size: 9vw;
    }
    .jumbotron p {
        font-size: 3vw;
    }
}

@media (max-width: 480px) {
    .jumbotron h1 {
        font-size: 14vw;
    }
    .jumbotron p {
        font-size: 7vw;
    }
}